import {Component, Input} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {Icon} from "@util/icon";

@Component({
   templateUrl: "icon-item.html",
   styleUrls: ["icon-item.scss"],
   selector: "vsan-icon-item",
})
export class IconItemComponent {

   @Input()
   label: string;

   @Input()
   iconClass: string;

   @Input()
   shape: string;

   @Input()
   missingLabel: string = VsanUiUtils.getString("vsan.na.label");

   @Input()
   copyToClipboard: boolean;

   @Input()
   isIconSolid: boolean = true;

   get clrIconClass(): string {
      let iconClass: string = this.iconClass;
      if (this.isIconSolid) {
         iconClass += ` ${Icon.CLR_SOLID_ICON_CLASS}`;
      }
      return iconClass;
   }
}